.Box {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #23252C;
  height: 950px;
  width: 450px;
  font-family: 'Inter', sans-serif;
}


.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 800px;
}

.photo {
  width: 400px;
}

.Content {
  background-color: #1A1B21;
  color: #fff;
}

.Header {
  text-align: center;
  margin: 0;
}

.fd {
  color: #F3BF99;
  font-size: 18px;
  margin-top: -15px;
}

.button {
  display: flex;
  justify-content: space-around;
}

.Email {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 18px;
  width: 100px;
  font-size: 18px;
  padding: 15px 75px;
  border: 1px solid #D1D5DB;
  border-radius: 5px;
  cursor: pointer;
}

.LinkedIn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #5093E2;
  color: #fff;
  height: 18px;
  width: 100px;
  font-size: 18px;
  padding: 15px 75px;
  border: 1px solid #D1D5DB;
  border-radius: 5px;
  cursor: pointer;
}

.button p {
  margin-left: 15px;
}

.about h3 {
  margin: 20px 10px 0 10px;
  font-size: 24px;
  font-weight: 100;
}

.about h4 {
  font-size: 18px;
  font-weight: 100;
  margin-bottom: 25px;
}

.Socials {
  width: 100%;
  height: 100%;
  background-color: #161619;
}

.Socials button {
  height: 33px;
  width: 33px;
  font-size: 24px;
  border-radius: 5px;
  background-color: #918E9B;
  margin: 20px 25px 25px 20px;
  padding-right: 28px;
}